import React, { Component } from 'react';
import Checkbox from './CheckBox'


class Todo extends Component {
	
	styleMark = () => {
		const trigg = this.props.todo.completed;
		return {
			color: trigg? '#ccc' : '#555',
			textDecoration: trigg ? 'line-through' : 'none'
		}
	}
	
	styleDate = () => {
		const trigg = this.props.todo.completed;
		return {
			color: trigg? '#ccc' : '#999',
			textDecoration: trigg ? 'line-through' : 'none'
		}
	}
	
	render() {
		const todo = this.props.todo;
		
		return (
			<div className="todo-container" style={{display: todo.show ? 'block' : 'none'}}>
			
				<Checkbox id={todo.id} completed={todo.completed}  markComplete={this.props.markComplete}/>
				
				<div className="todo-description-container">
					<p style={this.styleMark()} className="todo-title">{todo.title}</p>
					<p style={this.styleDate()} className="todo-date"> {todo.date}</p>
				</div>
				
				<button onClick={this.props.deleteTodo.bind(this, todo.id)} className="btn-del" >&times;</button> 
			</div>
		);
	}
}




export default Todo;