import React from 'react';
import Search from './components/Search';
import TodoList from './components/Todo_List';
import AddTodo from './components/Add_Todo';
import './main.css';

class TodoApp extends React.Component {
	
  state = {
	todos: [
		{
			id: 1,
			title: 'Pay Bill',
			date: 'Created Mar 21 1st @ 9:03 am',
			completed: false,
			show: true,
		},
		{
			id: 2,
			title: 'Take out the trash',
			date: 'Created Mar 21 1st @ 9:03 am',
			completed: true,
			show: true,
		},
		{
			id: 3, 
			title: 'Meet up with boss',
			date: 'Created Mar 21 1st @ 9:03 am',
			completed: false,
			show: true,
		},
		{
			id: 4, 
			title: 'Wash my dog',
			date: 'Created Mar 21 1st @ 9:03 am',
			completed: true,
			show: true,
		}
	]
  }  
	
  markComplete = (id) => {
	this.setState({todos: this.state.todos.map((todo) => {
			if(todo.id === id) {
				todo.completed = !todo.completed;
			}
			return todo;
		})
	});
  }
	
  
  addTodo = (title) => {
	const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
	const suffix = ['st', 'nd', 'rd', 'th'];
	const { todos } = this.state;
	const date = new Date();
	const hour = this.stamp(date.getHours());
	const minute = this.stamp(date.getMinutes());
	const dates = this.stamp(date.getDate());
	let dsuffix;
	if((+dates > 0 && +dates < 4) || (+dates > 20 && +dates < 24)){ dsuffix = suffix[+dates-1]; console.log('its happening', +dates) }else { dsuffix = suffix[3]; }
	console.log(dsuffix, dates)
	const month = months[date.getMonth()];
	const year = date.getFullYear();
	
	const newTodo = {
		id: 1,
		title: title,
		date: `Created ${month}, ${dates+dsuffix}  ${year} @ ${hour}:${minute} am`,
		completed: false,
		show: true
	};
	
	if(this.state.todos.length > 0) {
		newTodo.id = (todos[todos.length-1].id+1);
		this.setState({ todos: [...todos, newTodo]});
	}else {
		this.setState({ todos: [...todos, newTodo]});
	}
  }
	
	
  stamp(obj) {
	  return (obj < 10 ? '0'+obj : obj);
  }

  deleteTodo = (id) => {
	this.setState({ todos: [...this.state.todos.filter(todo => todo.id !== id)] });
  }
  
  searchTodo = (search) => {
	let word='';
	search = search.toUpperCase();
	
	if(search === '' || search === undefined || search === ' ' || search === null){
		this.setState( { todos: [...this.state.todos.filter(todo => {
				todo.show = true;
				return todo;
			})]
		});
	} else {
		this.setState( { todos: [...this.state.todos.filter(todo => {
				word = todo.title.toUpperCase();
				if(word.indexOf(search) > -1 && search !== ' ') {
					todo.show = true;
				}else {
					todo.show = false;
				} 
				return todo;
			})]
		});
	}
  }
	
  showCompleted = (checkedonly) => {
	if(checkedonly) {
		this.setState( { todos: [...this.state.todos.filter(todo => {
				todo.show = (todo.completed === true) ? true : false;
				return todo;
			})]
		});
	}else {
		this.setState( { todos: [...this.state.todos.filter(todo => {
				todo.show = true; 
				return todo;
			})]
		});
	}
  }
	
  render() {
    return(
		<div className="container">
			<h1 style={title}>To Do App</h1>
			<Search searchTodo={this.searchTodo} showCompleted={this.showCompleted}/>
			<TodoList todos={this.state.todos} markComplete={this.markComplete} deleteTodo={this.deleteTodo}/>
			<AddTodo addTodo={this.addTodo}/>
		</div>
    );
  }
} 

const title = {
	fontSize: '2.2rem'
}

export default TodoApp;
