import React, { Component } from 'react';

class AddTodo extends Component {
	
	state = {
		title: ''
	}
	
	onChange = (e) => {
		this.setState({ title: e.target.value });
	}
	
	onSubmit = (e) => {
		e.preventDefault();
		this.props.addTodo(this.state.title);
		this.setState({title: ''})
	}
	
	render() {
		return(
			<form onSubmit={this.onSubmit} className="component-container">
				<input id="add" className="input" placeholder="What do you need to do?" 
				value={this.state.title} onChange={this.onChange}
				/>
				
				<button className="btn-large" type="submit">Add Todo</button>
			</form>
		);
	}
}

export default AddTodo;