import React, { Component } from 'react';


class Checkbox extends Component {
	
	getStyle = () => {
		return {
			border: '1px solid #ddd',
			width: '15px',
			height: '15px',
			overflow: 'hidden',
			boxShadow: 'var(--component-shadow)',
			borderRadius: '3px',
			background: this.props.completed ? 'steelblue' : 'white',
			float: 'left',
			position: 'absolute',
			top: '30%',
			opacity: '0.8',
			textDecoration: 'line-through',
			cursor: 'pointer'
		}
	}
	
	
	
	render() {
		return(
			<div style={this.getStyle()} 
			onClick={this.props.markComplete.bind(this, this.props.id)}>
				<div className="right"></div>
			</div>
		);
	}
}




export default Checkbox;