import React, { Component } from 'react';


class Search extends Component {
	state = {
		search: '',
		checkedonly: false,
	}
	
	searchInput = (e) => {
		this.setState({ search: e.target.value });
		this.props.searchTodo(e.target.value);
	}
	
	check = (e) => {
		this.setState({ checkedonly: !e.target.checked });
		this.props.showCompleted(!this.state.checkedonly);
	}
	
	render() {
		return (
			<div className="component-container">
				<input type="text" id="search" 
				className="input"
				placeholder="Search todos"
				value={this.state.search}
				onChange = {this.searchInput} />
				
				<div className="search-option-container">
					<input type="checkbox" id="show_cp" onChange={this.check} />
					<label htmlFor="show_cp" className="search-checkbox-label">Show completed todos</label>
				</div>
			</div>
		);
	}
}

export default Search;